import * as React from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import ErrorBoundary from './components/ErrorBoundary';
import Home from './components/Home';
import Header from './components/Header';
import LoadingComponent from './components/LoadingComponent';
import { ThemeProvider } from '@mui/material/styles';
import { homeTheme } from './homeTheme';
import { apiCaller } from './services/apiCaller';
import { useDispatch, useSelector } from 'react-redux';
import { updateAuthState, updateSnackbarState } from './reducer/commonSlice';
import { Alert, Snackbar } from '@mui/material';
const About = React.lazy(() => import('./components/About'));
const Contact = React.lazy(() => import('./components/Contact'));
const Profile = React.lazy(() => import('./components/Profile'));
const LoginSignIn = React.lazy(() => import('./components/LoginSignIn'));
const Posts = React.lazy(() => import('./components/Gigs'));
const CreateGig = React.lazy(() => import('./components/Gigs/createGig.js'));
function App() {
  const routeMap = [
    { path: '/edaddress/', component: <Home /> },
    { path: '/edaddress/about', component: <About /> },
    { path: '/edaddress/contact', component: <Contact /> },
    { path: '/edaddress/profile', component: <Profile /> },
    { path: '/edaddress/posts', component: <Posts /> },
    { path: '/edaddress/create', component: <CreateGig /> },
    { path: '/edaddress/login', component: <LoginSignIn /> },
    { path: '/edaddress/*', component: <Home /> },
  ];
  const dispatch = useDispatch();
  React.useEffect(() => {
    const token = localStorage.getItem('token');
    apiCaller(
      '/isAuth',
      'post',
      {},
      {
        headers: {
          'x-access-token': token ?? '',
        },
      }
    ).then((res) => {
      const { isLoggedIn, id, username } = res;
      if (isLoggedIn)
        dispatch(
          updateAuthState({
            username,
            id,
            isLoggedIn,
            token: token,
          })
        );
      else dispatch(updateAuthState({ isLoggedIn: false }));
    });
  }, []);
  const handleSnackbarClose = () => {
    dispatch(updateSnackbarState({ open: false }));
  };
  const { open, message, type } = useSelector(
    (state) => state?.commonReducer?.snackbar
  );
  return (
    <ThemeProvider theme={homeTheme}>
      <ErrorBoundary>
        <>
          <Snackbar
            autoHideDuration={3000}
            open={open}
            onClose={handleSnackbarClose}
            color='primary'
            variant='outlined'
          >
            <Alert onClose={handleSnackbarClose} severity={type}>
              {message}
            </Alert>
          </Snackbar>
          <BrowserRouter>
            <Header />
            <Routes>
              {routeMap.map((route, index) => {
                return (
                  <Route
                    key={index}
                    path={route.path}
                    element={
                      <React.Suspense fallback={<LoadingComponent />}>
                        {route.component}
                      </React.Suspense>
                    }
                  />
                );
              })}
            </Routes>
          </BrowserRouter>
        </>
      </ErrorBoundary>
    </ThemeProvider>
  );
}

export default App;
