import axios from 'axios';
import { put, takeLatest } from 'redux-saga/effects';
import { fetchUserData, fetchUserDataSuccess } from '../reducer/userSlice';
function* fetchUserSaga(action) {
  try {
    const { data } = yield axios.get(`${process.env.DOMAIN_PATH}/user`, {
      headers: {
        'x-access-token': action.payload?.token ?? '',
      },
    });
    const postsData = yield axios.get(
      `${process.env.DOMAIN_PATH}/gigs/${action?.payload?.id}`
    );
    yield put(
      fetchUserDataSuccess({ ...data.data, posts: [...postsData.data.data] })
    );
  } catch (err) {
    console.error(err);
  }
}

export function* watchfetchUserSaga() {
  yield takeLatest(fetchUserData.type, fetchUserSaga);
}
