import axios from 'axios';
import { put, takeLatest } from 'redux-saga/effects';
import { fetchGigsData, fetchGigsDataSuccess } from '../reducer/gigsSlice';
function* fetchGigsSaga() {
  try {
    const { data } = yield axios.get(process.env.DOMAIN_PATH + '/gigs');
    yield put(fetchGigsDataSuccess(data.data));
  } catch (err) {
    console.error(err);
  }
}

export function* watchfetchGigsSaga() {
  yield takeLatest(fetchGigsData.type, fetchGigsSaga);
}
