import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import useMediaQuery from '@mui/material/useMediaQuery';
import {
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Close } from '@mui/icons-material';
import { useTheme } from '@mui/styles';
import { useSelector } from 'react-redux';

const Navigation = ({ classes }) => {
  const menu = [
    {
      name: 'Home',
      path: '/edaddress/',
    },
    {
      name: 'About Us',
      path: '/edaddress/about',
    },
    {
      name: 'Contact Us',
      path: '/edaddress/contact',
    },
    {
      name: 'Posts',
      path: '/edaddress/posts',
    },
    {
      name: 'Login / Register',
      path: '/edaddress/login',
      authRequired: false,
    },
    {
      name: 'Profile',
      path: '/edaddress/profile',
      authRequired: true,
    },
  ];
  const matches = useMediaQuery('(min-width:500px)');
  const [drawer, setDrawer] = useState(false);
  const theme = useTheme();
  const { isLoggedIn } = useSelector((state) => state.commonReducer.authState);
  const handleDrawerToggle = () => {
    setDrawer(!drawer);
  };

  const ExpandedNav = menu.map((item, index) =>
    item?.authRequired === undefined || item?.authRequired === isLoggedIn ? (
      <NavLink
        to={item.path}
        className={classes.item}
        key={index}
        style={({ isActive }) => {
          return {
            borderBottom: isActive
              ? `0.1rem solid ${theme.palette.text.primary}`
              : '',
          };
        }}
      >
        {item.name}
      </NavLink>
    ) : null
  );

  const collapseNav = (
    <IconButton onClick={handleDrawerToggle}>
      <MenuIcon
        sx={{
          color: theme.palette.text.primary,
          fontSize: '3rem',
          cursor: 'pointer',
        }}
        className={classes.brandName}
      />
    </IconButton>
  );
  return (
    <div className={classes.navigation}>
      {matches ? ExpandedNav : collapseNav}
      {!matches && (
        <Drawer
          anchor='right'
          open={drawer}
          elevation={10}
          classes={{ paper: classes.drawer }}
          onClose={() => setDrawer(false)}
        >
          <List>
            <ListItem>
              <ListItemText
                disableTypography
                primary={'Ed. Address'}
                sx={{
                  color: theme.palette.text.primary,
                  fontSize: '2rem',
                  cursor: 'pointer',
                  margin: '1rem',
                }}
              />
              <Close
                sx={{
                  color: theme.palette.text.primary,
                  fontSize: '3rem',
                  cursor: 'pointer',
                }}
                onClick={handleDrawerToggle}
              />
            </ListItem>
            {menu.map((item, index) =>
              item?.authRequired === undefined ||
              item?.authRequired === isLoggedIn ? (
                <ListItem button key={index}>
                  <NavLink
                    onClick={handleDrawerToggle}
                    to={item.path}
                    className={classes.drawerItem}
                    key={index}
                    style={({ isActive }) => {
                      return {
                        color: isActive ? theme.palette.text.primary : null,
                      };
                    }}
                  >
                    {item.name}
                  </NavLink>
                </ListItem>
              ) : null
            )}
          </List>
        </Drawer>
      )}
    </div>
  );
};

Navigation.defaultProps = {};
export default Navigation;
