import { createSlice } from '@reduxjs/toolkit';

export const initialState = {
  loading: true,
  data: [],
};

const gigsSlice = createSlice({
  name: 'gigs',
  initialState,
  reducers: {
    fetchGigsData: (state) => {
      state.loading = true;
    },
    fetchGigsDataSuccess: (state, action) => {
      state.data = action.payload;
      state.loading = false;
    },
  },
});

export const { fetchGigsData, fetchGigsDataSuccess } = gigsSlice.actions;
const gigsReducer = gigsSlice.reducer;
export default gigsReducer;
